<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>客户管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">预收冲应收</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}预收冲应收</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline" :model="params" ref="formInline">
        <el-form-item label="财务组织" size="mini" prop="company_id" :rules="rules.company_id">
          {{(function(){if(params.company_id == 0)params.company_id = null})()}}
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable :disabled="isDisabled">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="单据号" size="mini">
          <el-input v-model="params.deposits_sn" type="text" disabled></el-input>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker type="date" v-model="params.business_time" placeholder="选择日期" value-format="yyyy-MM-dd" :disabled="isDisabled" :clearable="false">
          </el-date-picker>
        </el-form-item>

        <el-form-item label="客户名称" size="mini" prop="customer_id" :rules="rules.customer_id">
          {{(function(){if(params.customer_id == 0)params.customer_id = null})()}}
          <el-select v-model="params.customer_id" filterable placeholder="请输入" @change="changeCustomer" :disabled="isDisabled" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <!-- <el-select clearable v-model="params.department_id" @focus="getDepartmentFocus" @change="departmentChange" placeholder="请选择" filterable :disabled="isDisabled">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList.options"></el-option>
          </el-select> -->
          <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" @change="departmentChange" placeholder="请选择" filterable :disabled="isDisabled">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="业务员" size="mini">
          {{(function(){if([0,1,2].includes(params.business_id) || params.company_admin_id > 0)params.business_id = null})()}}
          <!-- <el-select clearable v-model="params.business_id" @focus="getemployeeFoucs" @change="employeeChange" filterable :disabled="isDisabled">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList.options"></el-option>
          </el-select> -->
          <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok" @change="employeeChange" filterable :disabled="isDisabled">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="备注" size="mini">
          <el-input v-model="params.remark" type="text" maxlength="300"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="paymanageTitle">
      <i></i>
      <span>结算明细</span>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" @selection-change="handleSelectionChange" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="结算方式">
          <template slot-scope="scope">
            预收冲应收
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="bank_id" label="银行帐户">
          <template slot-scope="scope">
            预收账户
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="common_price" label="金额">
          <template slot-scope="scope">
            <!-- @keyup.enter.native="changePrice(scope.row,scope.$index)" -->
            <el-input size="small" type="number" class="editor" v-model="scope.row.common_price" @change="handleEdit(scope.row.price, scope.row)" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          <template slot-scope="scope">
            <el-input v-model="scope.row.remark" maxlength="300" class="editor" suffix-icon="el-icon-edit-outline"></el-input>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <span class="attention">*输入金额后请按Enter键进行确认 </span> -->
    <div class="aboutList" v-if="is_disabled">
      <div class="paymanageTitle ">
        <i></i>
        <span>核销明细</span>
      </div>
      <div class="table_container">
        <div>
          <el-table :highlight-current-row="true" tooltip-effect="light" :header-cell-class-name="cellClass" border :data="relationTable.tableData" show-summary :summary-method="getrelationTableSummaries" max-height="600">
            <el-table-column :show-overflow-tooltip="true" width="46">
              <template slot-scope="scope">
                <el-checkbox :key="scope.bill_id" v-model="scope.row.bill_checked" @change="clickCheckBox(scope.row,scope.row.bill_checked)"></el-checkbox>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" label="序号" width="46" align="center">
              <template slot-scope="scope">
                {{scope.$index+1}}
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_sn" label="单据号" width="146"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="order_type" label="单据类型"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_num" label="出库数量" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="order_price" label="出库金额" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="ec_channel_fee" label="通道费" align="right"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="sc_price" label="实际应收金额" align="right" width="120" :render-header="renderHeader">
              <template slot-scope="scope">
                <pre>{{scope.row.sc_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="156"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" width="156"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" width="156"></el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="rc_price" label="已冲销金额" align="right">
              <template slot-scope="scope">
                <pre>{{scope.row.rc_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="rest_price" label="未冲销金额" align="right">
              <template slot-scope="scope">
                <pre>{{scope.row.rest_price  | priceFormatNone}}</pre>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="bill_price" label="本次冲销金额" align="right">
              <template slot-scope="scope">
                <el-input v-model="scope.row.bill_price" type="number" class="editor" suffix-icon="el-icon-edit-outline"></el-input>
              </template>
            </el-table-column>
            <el-table-column :show-overflow-tooltip="true" property="payment_method" label="回款方式" width="156"></el-table-column>
          </el-table>
        </div>

      </div>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submitForm('formInline')">确 定</el-button>
    </div>
  </div>
</template>

<script>
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "customeDepositsAdd",
  data() {
    return {
      authority: {},
      title: "添加",
      params: {
        remark: "",
        business_id: null,
        department_id: null
      },
      TableData: {
        tableData: [{}]
      },
      companyList: [],
      multipleSelection: [],
      per_page: [30, 50, 100, 500],
      index: 0,
      customer_id: null, //客户名称
      deposits_id: null,
      customer_name: "",
      isDisabled: false,
      loading: false,
      is_disabled: false,
      relationTable: {
        tableData: []
      },
      changeIndex: 0
    };
  },
  computed: {
    ...mapGetters(["depositsAdd_showGet"])
  },
  methods: {
    ...mapActions(["depositsIndexACT_show", "depositsIndexACT"]),
    renderHeader() {
      return (
        <el-tooltip class="item" effect="dark" placement="top">
          <p slot="content">实际应收金额=本出库单中出库金额</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            实际应收金额
          </span>
        </el-tooltip>
      );
    },
    cellClass(row) {
      if (row.columnIndex == 0) {
        return "Disabledselection";
      }
    },
    employeeChange() {
      this.doDeposits(this.params);
    },
    changeTitle(index) {
      this.changeIndex = index;
    },
    handleEdit(index, val) {
      this.$nextTick(() => {
        val.common_price = val.common_price.toFixed(2);
      });
    },
    clickCheckBox(row, bill_checked) {
      let vm = this;
      let table = [];
      for (let i = 0; i < this.TableData.tableData.length; i++) {
        table.push({
          // receivables_mode_id: this.TableData.tableData[i].receivables_mode_id,
          // bank_id: this.TableData.tableData[i].bank_id,
          common_price: this.TableData.tableData[i].common_price,
          remark:
            this.TableData.tableData[i].remark == undefined
              ? ""
              : this.TableData.tableData[i].remark
        });
      }
      let billBody = [];

      this.relationTable.tableData[
        this.relationTable.tableData.indexOf(row)
      ].checked = bill_checked == true ? 1 : 0;
      for (let i = 0; i < this.relationTable.tableData.length; i++) {
        billBody.push({
          bid: this.relationTable.tableData[i].bid,
          checked: this.relationTable.tableData[i].checked,
          bill_price: this.relationTable.tableData[i].bill_price
        });
      }
      let funcan = this.params;
      funcan.common_body = JSON.stringify(table);
      funcan.is_verify = 2;
      funcan.bid = row.bid;
      funcan.bill_body = JSON.stringify(billBody);
      this.doDeposits(funcan);
    },
    changePrice(row, index) {
      let vm = this;
      let table = [];
      for (let i = 0; i < this.TableData.tableData.length; i++) {
        if (this.TableData.tableData[i].common_price > 99999999.99) {
          this.$message.warning("金额输入不能大于一亿");
          this.TableData.tableData[i].common_price = 0;
          return false;
        } else if (this.TableData.tableData[i].common_price < -99999999.99) {
          this.$message.warning("金额输入不能小于负一亿");
          this.TableData.tableData[i].common_price = 0;
          return false;
        }
        table.push({
          receivables_mode_id: this.TableData.tableData[i].receivables_mode_id,
          bank_id: this.TableData.tableData[i].bank_id,
          common_price: this.TableData.tableData[i].common_price,
          remark:
            this.TableData.tableData[i].remark == undefined
              ? ""
              : this.TableData.tableData[i].remark
        });
      }
      let funcan = this.params;
      funcan.common_body = JSON.stringify(table);
      funcan.is_verify = 1;
      this.doDeposits(funcan);
    },
    doDeposits(params) {
      let { company_id, customer_id } = this.params;
      if (!(company_id && customer_id)) return false;
      if (this.depositsAdd_showGet.deposits_id) {
        params.deposits_id = this.depositsAdd_showGet.deposits_id;
      }
      this.$axMethod.getDepositsDetailed(params).then(res => {
        if (res.data.code == 400 || res.data.data.length == 0) {
          this.is_disabled = false;
          this.relationTable.tableData = [];
          this.params.bill_body = [];
        } else {
          this.is_disabled = true;
          this.relationTable.tableData = res.data.data;
          let checkArr = [];
          for (let i = 0; i < this.relationTable.tableData.length; i++) {
            if (this.relationTable.tableData[i].checked == true) {
              checkArr.push(this.relationTable.tableData[i]);
            }
          }
          let arr = [];
          for (let i = 0; i < checkArr.length; i++) {
            arr.push({
              index: this.relationTable.tableData.indexOf(checkArr[i])
            });
          }
          for (let i = 0; i < arr.length; i++) {
            this.relationTable.tableData[arr[i].index].bill_checked = true;
          }
        }
      });
    },
    submitForm(formInline) {
      //表单提交
      this.$refs[formInline].validate(valid => {
        if (valid) {
          if (this.TableData.tableData.length < 1) {
            this.$message.warning("请完善表格信息");
            return false;
          }
          if (this.relationTable.tableData.length <= 0) {
            this.$message.warning("您暂无核销明细，不能进行预收冲应收");
            return false;
          }
          let vm = this;
          let table = [];
          for (let i = 0; i < this.TableData.tableData.length; i++) {
            table.push({
              receivables_mode_id: this.TableData.tableData[i]
                .receivables_mode_id,
              bank_id: this.TableData.tableData[i].bank_id,
              common_price: this.TableData.tableData[i].common_price,
              remark:
                this.TableData.tableData[i].remark == undefined
                  ? ""
                  : this.TableData.tableData[i].remark
            });
          }
          let funcan = this.params;
          funcan.common_body = JSON.stringify(table);
          funcan.is_verify = 0;
          // let billBody = [];
          let billBody = this.relationTable.tableData.map(item => {
            item.checked = item.checked;
            item.bill_price = item.bill_price;
            item.bid = item.bid;
            return item;
          });
          funcan.bill_body = JSON.stringify(billBody);
          let api = !this.depositsAdd_showGet.deposits_id
            ? "addDeposits"
            : "editDeposits";
          if (api == "editDeposits")
            funcan.deposits_id = this.depositsAdd_showGet.deposits_id;
          vm.$axios({
            method: "post",
            url: vm.$api[api],
            data: funcan
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: res.data.msg || res.data.data,
                type: "success"
              });
              vm.goBack();
            }
          });
        }
      });
    },

    changeCustomer(val) {
      if (!val) return;
      let obj = {};
      obj = this.customerList.find(item => {
        if (item.customer_id === val) {
          this.customer_name = item.customer_name;
        }
      });
      this.TableData.tableData = [{}];
      if (val) {
        var company_id = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].company_id;
        var associated_customer_id = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].associated_customer_id;
        this.params.associated_customer_id = associated_customer_id;
      }
      let params = {
        customer_id: this.params.customer_id,
        associated_customer_id: associated_customer_id,
        company_id: company_id
      };
      this.$axMethod
        .getCustomerInfoNEWqx(params)
        .then(({ data }) => {
          if (data.code == 200) {
            this.$set(
              this.TableData.tableData[0],
              "common_price",
              Number(data.data.deposits).toFixed(2)
            );

            this.$set(this.params, "business_id", data.data.business_id); //业务员
            return data.data;
          }
        })
        .then(customeInfo => {
          if (customeInfo && customeInfo.business_id) {
            //有业务员
            this.$axMethod
              .getEmployeeInfoMore({ employee_id: customeInfo.business_id })
              .then(res3 => {
                this.employeeList = [
                  {
                    employee_id: res3.data.data.employee_id,
                    employee_name: res3.data.data.employee_name
                  }
                ];
                let table = [];
                for (let i = 0; i < this.TableData.tableData.length; i++) {
                  if (this.TableData.tableData[i].common_price == undefined) {
                    table = [];
                  } else {
                    table.push({
                      receivables_mode_id: this.TableData.tableData[i]
                        .receivables_mode_id,
                      bank_id: this.TableData.tableData[i].bank_id,
                      common_price: this.TableData.tableData[i].common_price,
                      remark:
                        this.TableData.tableData[i].remark == undefined
                          ? ""
                          : this.TableData.tableData[i].remark
                    });
                  }
                }

                let funcan = this.params;
                funcan.common_body = JSON.stringify(table);
                funcan.is_verify = 1;
                this.doDeposits(funcan);
              });
          } else {
            let table = [];
            for (let i = 0; i < this.TableData.tableData.length; i++) {
              if (this.TableData.tableData[i].common_price == undefined) {
                table = [];
              } else {
                table.push({
                  receivables_mode_id: this.TableData.tableData[i]
                    .receivables_mode_id,
                  bank_id: this.TableData.tableData[i].bank_id,
                  common_price: this.TableData.tableData[i].common_price,
                  remark:
                    this.TableData.tableData[i].remark == undefined
                      ? ""
                      : this.TableData.tableData[i].remark
                });
              }
            }

            let funcan = this.params;
            funcan.common_body = JSON.stringify(table);
            funcan.is_verify = 1;
            this.doDeposits(funcan);
          }
        });
    },
    goBack() {
      this.depositsIndexACT({
        params: this.depositsAdd_showGet.params,
        goPage: this.depositsAdd_showGet.goPage,
        pageSize: this.depositsAdd_showGet.pageSize
      });
      this.$router.push({
        path: "/custom/deposits"
      });
    },
    addPaymangeFun() {
      //增行
      if (
        this.params.customer_id == undefined ||
        this.params.customer_id == ""
      ) {
        this.$message({
          message: "请选择客户名称",
          type: "warning"
        });
        return false;
      }
      this.TableData.tableData.push({});
    },
    delFun() {
      //删除
      if (this.multipleSelection.length < 1) {
        this.$message.warning("请选择要删除的数据");
        return false;
      }
      for (var i = 0; i < this.multipleSelection.length; i++) {
        var index = this.TableData.tableData.indexOf(this.multipleSelection[i]);
        this.TableData.tableData.splice(index, 1);
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    companyChange(val) {
      this.TableData.tableData = [{}];
      this.relationTable.tableData = [];
      this.$set(this.params, "customer_id", "");
    },
    departmentChange(val) {
      this.doDeposits(this.params);
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 2) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    },
    getrelationTableSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 5) {
            sums[index] = sums[index];
          } else if ([6, 7, 8, 14, 12, 13].includes(index)) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });

      return sums;
    }
  },
  computed: {
    ...mapGetters(["depositsAdd_showGet"])
  },
  created() {
    if (this.depositsAdd_showGet.deposits_id) {
      this.isDisabled = true;
      this.$axios({
        method: "get",
        url: this.$api.getDepositsInfo,
        params: {
          deposits_id: this.depositsAdd_showGet.deposits_id,
          is_edit: 1
        }
      }).then(({ data }) => {
        if (data.code == 200) {
          this.$set(this, "TableData", data.data);
          this.relationTable.tableData = data.data.receipt_common_bill;
          this.params = {
            business_time: !this.TableData.business_time
              ? ""
              : this.TableData.business_time,
            customer_id: this.TableData.customer_id,
            company_id: this.TableData.company_id,
            department_id: this.TableData.department_id,
            business_id: this.TableData.business_id,
            remark: this.TableData.remark,
            receipt_sn: this.TableData.receipt_sn
          };

          this.TableData.tableData = this.TableData.deposits_common;
          this.deposits_id = this.depositsAdd_showGet.deposits_id;

          this.companyList = [
            {
              company_name: data.data.company_name,
              company_id: data.data.company_id
            }
          ];
          this.departmentList = [
            {
              department_name: data.data.department_name,
              department_id: data.data.department_id
            }
          ];
          this.employeeList = [
            {
              employee_name: data.data.business_name,
              employee_id: data.data.business_id
            }
          ];
          this.customerList = [
            {
              customer_name: data.data.customer_name,
              customer_id: data.data.customer_id,
              contact_name: data.data.contact_name,
              customer_mobile: data.data.customer_mobile,
              address: data.data.address
            }
          ];
        }

        let vms = this;
        let funcan = {
          business_time:
            this.TableData.business_time == undefined
              ? ""
              : this.TableData.business_time,
          customer_id: this.TableData.customer_id,
          company_id: this.TableData.company_id,
          department_id: this.TableData.department_id,
          business_id: this.TableData.business_id,
          remark: this.TableData.remark
        };
        funcan.common_body = JSON.stringify(data.data.tableData);
        funcan.is_verify = 1;
        funcan.deposits_id = this.depositsAdd_showGet.deposits_id;
        this.$axios({
          method: "get",
          url: vms.$api.getDepositsInfo,
          params: {
            deposits_id: this.depositsAdd_showGet.deposits_id,
            is_edit: 1
          }
        }).then(res => {
          let checkDefaultArr = [];
          for (let i = 0; i < vms.relationTable.tableData.length; i++) {
            if (vms.relationTable.tableData[i].checked == 1) {
              checkDefaultArr.push(vms.relationTable.tableData[i]);
            }
          }
          let arr = [];
          for (let i = 0; i < checkDefaultArr.length; i++) {
            arr.push({
              index: vms.relationTable.tableData.indexOf(checkDefaultArr[i])
            });
          }
          for (let i = 0; i < arr.length; i++) {
            vms.relationTable.tableData[arr[i].index].bill_checked = true;
          }

          if (res.data.code == 400 || res.data.data.length == 0) {
            vms.is_disabled = false;
          } else {
            vms.is_disabled = true;
          }
        });
      });
    } else {
      let { employee_id, company_admin_id } = localStorage;
      if ([0, 1, 2].includes(Number(employee_id)) || company_admin_id > 0) {
        this.$set(this, "params", {
          company_id: Number(localStorage.company_id),
          business_time: this.$publicFution.formatData(new Date())
        });
      } else {
        this.$set(this, "params", {
          company_id: Number(localStorage.company_id),
          department_id: 0,
          business_id: 0,
          business_time: this.$publicFution.formatData(new Date())
        });
      }

      // bindIdsF(this, localStorage, [
      //   ["company_id", "company_name", "companyList"],
      //   ["department_id", "department_name", "departmentList", "options"],
      //   ["employee_id", "employee_name", "employeeList", "options"]
      // ]);
      opthionList(this, localStorage);
    }
  },
  mounted() {
    this.title =
      this.depositsAdd_showGet.deposits_id == undefined ? "添加" : "修改";
  }
};
</script>

